.contactUs{
  width: 98%;
  height: 28px;

  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-between;

  position: absolute;
  top: 93%;
  z-index: 3;

  font-family: Helvetica;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 28px;


  color: #9A9A9A;

  padding-right: 1%;
  padding-left: 1%;


}
.btnRight{
  width: 33%;
  height: 28px;

  display: flex;
  justify-content: flex-start;

  user-select: none;
}
.btnCenter{
  width: 33%;
  height: 28px;

  display: flex;
  justify-content: center;
}
.btnLeft{
  width:33%;
  height: 28px;

  display: flex;
  justify-content: flex-end;

  user-select: none;
}
.btnText{
  width: max-content;

  user-select: none;
  cursor: pointer;
}
.icon{
  font-size: 1.8vw;
  padding: 5px;
  border-radius: 25%;
  margin: 0 5px;
}
.iconContainer{
  display: flex;
  background: #15161A;
}
